.txtBox {
  width: 80%;
  margin: 0 auto;
}
.txtBox h3 {
  color: var(--color-sub1);
  font-size: 20px;
  font-weight: 900;
  line-height: 24px;
  margin-bottom: 20px;
}
.txtBox p {
  margin-bottom: 10px;
  line-height: 1.5;
}
.txtBox p strong {
 font-weight: 800;
}
.txtBox h3,
.txtBox p {
  opacity: 0;
  transform: translateY(-100px); /* 위로 올려놓기 */
}