.logo {
  width: 250px;
  margin: 0 auto;
}
.mainTxtWrapper {
  margin: 60px 0 100px;
}
.mainTxtWrapper h3 {
  font-size: 24px;
  font-weight: 900;
  line-height: 20px;
  color: var(--color-main);
  margin-bottom: 10px;
}
.mainStroke {
  z-index: 0;
  position: relative;
}
.mainStroke::before {
  z-index: -1;
  content: attr(data-content);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  -webkit-text-stroke: 8px var(--color-sub2);
}

.mainTxtWrapper h3 {
  opacity: 0; /* 기본적으로 투명 */
  transform: translateY(-50px); /* 위로 이동 */
  transition: opacity 0.3s ease, transform 0.3s ease; /* 부드러운 전환 */
}