footer {
  padding: 22px 17px;
  text-align: left;
  color: var(--color-sub1);
}
.footerLogo {
  width: 110px;
}
.address {
  margin-top: 50px;
  font-size: 14px;
  line-height: 1.6;
}
.copyrights {
  margin: 40px 0;
}
.footerBottom {
  justify-content: space-between;
  align-items: start;
  padding-bottom: 100px;
}
.footerBottom h4 {
  font-weight: 700;
  line-height: 2;
}
.footerBottom p {
  line-height: 2;
}
.footerBottom p a {
  display: block;
  color: var(--color-sub2);
  font-weight: 500;
  text-transform: uppercase;
}
.footerRight p:last-child {
  text-decoration: underline;
  color: var(--color-sub2);
  padding-top: 30px;
}