/******************* Menu Button & Animation */
.menuWrapper {
  position: relative;
}
.icon {
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
  z-index: 10;
}

.icon span {
  position: absolute;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #333;
  transition: all 0.3s ease-in-out;
  border-radius: 2px;
}

.icon .bar1 {
  top: 0;
}

.icon .bar2 {
  top: 50%;
  transform: translateY(-50%);
}

.icon .bar3 {
  bottom: 0;
}

/* Menu Button Animation When Open */
.open .bar1 {
  transform: rotate(45deg) translate(5px, 5px);
}

.open .bar2 {
  opacity: 0; /* 숨김 */
}

.open .bar3 {
  transform: rotate(-45deg) translate(7px, -6.5px);
}

/******************* Menu Slide Animation */
.menu {
  position: fixed;
  top: -100%;
  left: 50%; /* 메뉴를 화면 밖으로 숨김 */
  transform: translateX(-50%);
  width: 430px;
  height: 33%;
  background-color: var(--color-main);
  transition: all 0.5s ease-in-out;
  z-index: 5;
  padding: 50px 20px 20px;
  box-sizing: border-box;
}

.menu.open {
  top: 0; /* 메뉴가 화면 안으로 슬라이드 */
}

.menu li {
  margin: 20px 0;
}
.menu span:last-child  {
  border-bottom: 1px solid var(--color-main);
}

.menu span {
  text-decoration: none;
  display: block;
  width: 100%;
  color: #fff;
  font-weight: 600;
  font-size: 18px;
  line-height: 2;
  border-bottom: 1px solid #fff;
  margin-bottom: 12px;
  cursor: pointer;
}
.menu span:hover {
  color: var(--color-sub2);
}
