@import url(./assets/styles/reset.min.css);
@import url(./assets/styles/pretendard-subset.css);
@import url('https://fonts.googleapis.com/css2?family=Bowlby+One+SC&display=swap');
/* font-family: "Bowlby One SC", sans-serif; */

body{
  font-family: "Pretendard", -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
  --bg-color: #F6F1EC;
  --color-main:#FFC700;
  --color-sub1:#F35000;
  --color-sub2:#451500;

  --font-ko: 'Pretendard','Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  --font-en: "Bowlby One SC", sans-serif; 
}

/****************** coommon */
html {
  background-color: #fff;
}
body {
  margin: 0 auto;
  text-align: center;
  width: 430px;
  background-color: var(--bg-color);
  font-family: var(--font-ko);
}
header {
  position: relative;
}
section {
  margin-bottom: 150px;
}

.flex {
  display: flex;
  align-items: center;
}
.grid {
  display: grid;
  place-content: center;
}
.stroke {
  -webkit-text-stroke: 4px var(--color-sub2);
}
.mainStroke {
  z-index: 0;
  position: relative;
}
.mainStroke::before {
  z-index: -1;
  content: attr(data-content);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  -webkit-text-stroke: 8px var(--color-sub2);
}
.title {
  font-family: var(--font-en);
  font-size: 60px;
  text-transform: uppercase;
  color: var(--color-main);
  text-align: center;
  line-height: 1;
  margin: 0 auto 60px;
}
.num {
  width: 42px; height: 42px;
  background-color: var(--color-sub1);
  color: #fff;
  text-align: center;
  line-height: 42px;
  border-radius: 25px;
  margin: 60px auto 0;
  font-size: 20px;
}
.subTitle {
  text-align: center;
  font-size: 20px;
  font-weight: 900;
  margin: 12px 0;
  line-height: 50px;
}
.mail {
  padding: 20px 25px;
  background-color: var(--color-sub1);
  border-radius: 40px;
  color: #fff;
  font-weight: 600;
  line-height: 1.8;
}
.mail:hover {
  background-color: var(--color-main);
  color: var(--color-sub2);
}
.btn {
  width: 344px; height: 56px;
  line-height: 56px;
  background-color: var(--color-sub1);
  border-radius: 30px;
  margin: 0 auto;
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
  position: fixed;
  bottom: 30px; left: 50%;
  transform: translateX(-50%);
  z-index: 99;
  cursor: pointer;
  user-select: none;
}
