.productsImg {
  overflow: hidden;
}

.cardList img {
  width: 150px
}
.cardList .listTitle p:first-child {
  width: 100%;
  text-align: left;
}
.card {
  gap: 20px;
  margin-bottom: 30px;
  animation: slid 20s linear infinite alternate
}
.card:nth-child(2) {
  transform: translateX(-160%);
  animation: slid2 20s linear infinite alternate
}
.cardList {
  width: 350px; height: 170px;
  background-color: #fff;
  border-radius: 20px;
  padding: 10px;
  gap: 10px;
  box-shadow: 2px 2px 10px #ddd;
}
.listTitle {
  flex-direction: column;
  justify-content: space-between;
  align-items: end;
  height: 90%; width: 170px;
  color: var(--color-sub1);
  text-transform: uppercase;
  font-weight: 500;
}

@keyframes slid {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes slid2 {
  0% {
    transform: translateX(-160%);
  }
  100% {
    transform: translateX(0%);
  }
}
