.value {
  position: relative;
  z-index: 1;
}
.wrapper1 .valueImg img {
  width: 220px;
}
.wrapper1 .valueImg img:last-child {
  position: absolute;
  top: 22px; left: 28.5%;
  z-index: -1;
}
.wrapper2 .valueImg {
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  gap: 0 20px;
  width: 360px; height: 383px;
  margin: 0 auto;
}
.wrapper2 .valueImg img{
  width: 170px;
}
.wrapper2 .valueImg img:nth-child(2) {
  margin-top: 30px;
}
.wrapper2 .valueImg img:last-child {
  margin-top: 30px;
}
.wrapper3 .valueImg {
  z-index: 1;
  height: 245px; width: 200px;
  margin: 0 auto;
  position: relative;
}
.wrapper3 .valueImg img {
  width: 220px;
}
.wrapper3 .valueImg img:nth-child(2) {
  position: absolute;
  top: 12px; left: -15px;
  z-index: -1;
}
.wrapper3 .valueImg img:last-child {
  position: absolute;
  top: 22px; left: -20px;
  z-index: -2;
}
.value p {
  display: block;
  width: 280px;
  margin: 0 auto 80px;
  line-height: 1.4;
}
.wrapper2 p {
  width: 160px;
}
.wrapper3 p {
  width: 318px;
}