.logos img {
  width: 1175px;
  animation: slid 30s linear infinite alternate
}
.logos img:nth-child(even){
  transform: translateX(-70px);
  animation: slid2 30s linear infinite alternate
}

@keyframes slid {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-60%);
  }
}

@keyframes slid2 {
  0% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(0%);
  }
}