.listTitle span {
  display: block;
  background-color: var(--color-sub1);
  width: 19px; height: 1.7px;
  border-radius: 2px;
}
.arrowBar1 {
  transform: translateX(-10px);
}
.listTitle .arrowBar2 {
  transform: translate(2px, -4px) rotate(45deg);
   width: 8px;
   
}
.listTitle .arrowBar3 {
  transform: translate(2px, -1.5px) rotate(-45deg);
   width: 8px;
}